html {
  box-sizing: border-box;
  font-size: 62.5%;
}
@media (max-width: 480px) {
  html {
    font-size: 43.75%;
  }
}
@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 992px) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 1200px) {
  html {
    font-size: 70%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.7;
  color: #777;
  background-color: #000;
}

.contenedor {
  max-width: 120rem !important;
  width: 95%;
  margin: 0 auto !important;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 1.6rem;
}

h4 {
  font-size: 1.8rem;
}

h5 {
  font-size: 1.6rem;
}

.section {
  padding: 5rem 0 0 0;
}

.section__titulo {
  margin-bottom: 4rem;
}
.section__titulo h2 {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-weight: 400;
}

.section__parrafo {
  text-align: center;
  margin-bottom: 4rem;
}

h2:after {
  content: "";
  display: block;
  width: 5rem;
  height: 0.2rem;
  margin: 0 auto 0.5rem auto;
  background-color: #2ecc71;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/banner.avif);
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.barra {
  padding: 3rem;
}
.barra .contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.barra--sticky {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  padding: 1.5rem;
  background-color: #000;
}

.logo__nombre {
  color: #fff;
  font-weight: 700;
  font-size: 3.6rem;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}
.logo__nombre .logo__span {
  color: #2ecc71;
  font-weight: 400;
}

.navegacion {
  display: flex;
  gap: 2rem;
}
@media (max-width: 992px) {
  .navegacion {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 1000;
    width: 100%;
    height: 100vh;
    background-color: #000;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
}
.navegacion a {
  display: block;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.navegacion a:hover {
  color: #2ecc71;
}

@media (max-width: 992px) {
  .navegacion--active {
    left: 0;
  }
}

.navegacion--menu {
  color: #fff;
  background: none;
  border: none;
  font-size: 3rem;
  display: block;
  cursor: pointer;
  z-index: 1500;
}
@media (min-width: 992px) {
  .navegacion--menu {
    display: none;
  }
}

.header__texto {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  align-items: center;
  transform: translate(-50%, -50%);
}
@media (max-width: 992px) {
  .header__texto {
    flex-direction: column;
  }
}

.header__photo {
  display: block;
  width: 18rem;
  height: 18rem;
  margin-right: 2.5rem;
  border-radius: 50%;
}
@media (max-width: 992px) {
  .header__photo {
    margin-right: 0;
  }
}

.header__texto--primario {
  font-size: 4rem;
  font-weight: 700;
  color: #2ecc71;
  text-align: center;
}
.header__texto--primario span {
  display: block;
  font-size: 3rem;
  font-weight: 400;
  color: #fff;
}

@media (min-width: 992px) {
  .seccion__about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.seccion__about--item:not(:last-child) {
  margin-bottom: 4rem;
}

.about__titulo {
  margin-bottom: 4rem;
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 700;
}

.about__description {
  margin-bottom: 4rem;
}
.about__description span {
  color: #fff;
  font-size: 2.2rem;
  font-weight: 700;
}

.button {
  display: inline-block;
  padding: 1rem 1.5rem;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 1rem;
  cursor: pointer;
}

.button--primario {
  background-color: #2ecc71;
}

.button__icon {
  margin-right: 0.5rem;
  font-size: 1.8rem;
}

.about__galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}
@media (min-width: 992px) {
  .about__galeria {
    grid-template-columns: repeat(3, 1fr);
  }
}

.about__galeria--item {
  text-align: center;
}
.about__galeria--item:hover {
  border-bottom: 0px solid transparent;
}
@media (min-width: 992px) {
  .about__galeria--item:hover {
    color: #2ecc71;
    border-bottom: 3px solid #2ecc71;
    border-radius: 1rem;
  }
}

@media (min-width: 992px) {
  .about__galeria--item {
    border-bottom: 3px solid transparent;
    border-radius: 1rem;
  }
}

.about__galeria--icon {
  font-size: 5rem;
}

@media (min-width: 768px) {
  .services {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.services__item {
  padding: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .services__item {
    padding: 0;
  }
}
.services__item:hover {
  border-bottom: 0px solid transparent;
}
@media (min-width: 992px) {
  .services__item:hover {
    color: #2ecc71;
    border-bottom: 3px solid #2ecc71;
    border-radius: 1rem;
  }
}
.services__item h4 {
  color: #fff;
  font-weight: 400;
  text-transform: uppercase;
}

.services__icon {
  font-size: 5rem;
  margin-bottom: 1.5rem;
}

.services__description {
  color: #777 !important;
}

.portafolio__section {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .portafolio__section {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .portafolio__section {
    grid-template-columns: repeat(3, 1fr);
  }
}

.portafolio__item--figure {
  margin: 0;
  position: relative;
  background-color: #777;
  overflow: hidden;
}
.portafolio__item--figure:hover .portafolio__caption {
  transform: translate(0, 0);
  opacity: 1;
}
@media (min-width: 992px) {
  .portafolio__item:nth-child(1) {
    grid-column: 1/-1;
  }
}

.portafolio__photo {
  display: block;
  width: 100%;
}

.portafolio__caption {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
  transform: translate(0, 100%);
  opacity: 0;
}
@media (max-width: 992px) {
  .portafolio__caption {
    position: relative;
    width: auto;
    height: auto;
    padding: 2rem 0;
    background-color: #111;
    transform: translate(0, 0);
    opacity: 1;
  }
}

.portafolio__titulo {
  font-weight: 400;
  text-transform: uppercase;
  color: #2ecc71;
}
.portafolio__titulo h5 {
  margin: 1.5rem 0 0 0;
}

.portafolio__description {
  color: #fff;
  font-size: 1.5rem;
  margin: 0 0 1.5rem 0;
}

.certificates {
  display: grid;
}

.certificate__photo {
  width: 100%;
  border-radius: 1rem;
}

.contact {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .contact {
    grid-template-columns: repeat(3, 1fr);
  }
}

.contact__item {
  text-align: center;
  background-color: #111;
  border-radius: 0.5rem;
}

.contact__link {
  display: flex;
  flex-direction: column;
  padding: 6rem;
  text-decoration: none;
  color: inherit;
}

.contact__icon {
  font-size: 4rem;
}

.footer {
  padding: 4rem 0;
}

.footer__texto {
  text-align: center;
}
.footer__texto p span {
  display: block;
  font-size: 1.4rem;
}